export default ".main-footer {\n  border-top: 1px solid $light-dividers;\n  background: #262b31;\n}\n\n.landing .main-footer {\n  margin-top: 0;\n}\n\n.main-footer a:hover {\n  text-decoration: underline;\n  color: #fbd132;\n}\n\n.main-footer .container-center {\n  width: 60vw;\n  max-width: 1000px;\n  display: flex;\n  flex-direction: column;\n  flex-wrap: wrap;\n  justify-content: space-between;\n}\n\n.footer-top {\n  margin-bottom: 3%;\n}\n\n.footer-top a:hover {\n  text-decoration: none;\n  cursor: pointer;\n}\n\n.main-footer__logo img {\n  width: 110px;\n  height: 65px;\n  margin-top: 10px;\n}\n\n.main-footer__logo img:first-of-type {\n  width: 70px;\n}\n\n.footer-middle {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  margin-bottom: 5%;\n}\n\n.footer-middle ul {\n  padding: 0 0 40px;\n}\n\n.main-footer__social ul {\n  display: flex;\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n.main-footer__social a {\n  position: relative;\n  width: 50px;\n  height: 50px;\n  margin-right: 3px;\n  text-align: center;\n  border-radius: 50%;\n  background-color: rgba(0, 0, 0, 0.2);\n}\n\n.main-footer__vk-social-btn {\n  font-size: inherit;\n  position: absolute;\n  display: block;\n  width: 15px;\n  height: 15px;\n  content: \"\";\n  background: url(\"/assets/images/icons/icon-vk.svg\") no-repeat center;\n}\n\n.main-footer__vk-social-btn:hover {\n  background-color: #4c75a3;\n}\n\n.main-footer__fb-social-btn {\n  font-size: inherit;\n  position: absolute;\n  display: block;\n  width: 15px;\n  height: 15px;\n  content: \"\";\n  background: url(\"/assets/images/icons/icon-fb.svg\") no-repeat center;\n}\n\n.main-footer__fb-social-btn:hover {\n  background-color: #3b5998;\n}\n\n.main-footer__insta-social-btn {\n  font-size: inherit;\n  position: absolute;\n  display: block;\n  width: 15px;\n  height: 15px;\n  content: \"\";\n  background: url(\"/assets/images/icons/icon-insta.svg\") no-repeat center;\n}\n\n.main-footer_