dion ul li {\n  display: table-cell;\n  vertical-align: bottom;\n  position: relative;\n  width: 16.666%;\n  height: 600px;\n  background-repeat: no-repeat;\n  background-position: center center;\n  background-size: cover;\n  transition: all 500ms ease;\n}\n\n.accordion ul li div {\n  display: block;\n  overflow: hidden;\n  width: 100%;\n}\n\n.accordion ul li div a {\n  display: block;\n  height: 600px;\n  width: 100%;\n  position: relative;\n  z-index: 3;\n  vertical-align: bottom;\n  padding: 15px 20px;\n  box-sizing: border-box;\n  color: #fff;\n  text-decoration: none;\n  font-family: Open Sans, sans-serif;\n  background: #24292f79;\n  transition: all 200ms ease;\n}\n\n.accordion ul li div a * {\n  opacity: 0;\n  margin: 0;\n  width: 100%;\n  text-overflow: ellipsis;\n  position: relative;\n  z-index: 5;\n  white-space: nowrap;\n  overflow: hidden;\n  transform: translateX(-20px);\n  transition: all 400ms ease;\n}\n\n.accordion ul li div a h2 {\n  font-family: Montserrat, sans-serif;\n  text-overflow: clip;\n  font-size: 24px;\n  text-transform: uppercase;\n  margin-bottom: 2px;\n  top: 510px;\n}\n\n.accordion ul li div a p {\n  top: 510px;\n  font-size: 13.5px;\n}\n\n.accordion ul li:nth-child(1) {\n  background-image: url(\"/assets/images/games-wp/basketball.jpg\");\n}\n\n.accordion ul li:nth-child(2) {\n  background-image: url(\"/assets/images/games-wp/football.jpg\");\n}\n\n.accordion ul li:nth-child(3) {\n  background-image: url(\"/assets/images/games-wp/hockey.jpg\");\n}\n\n.accordion ul li:nth-child(4) {\n  background-image: url(\"/assets/images/games-wp/rugby.jpg\");\n}\n\n.accordion ul li:nth-child(5) {\n  background-image: url(\"/assets/images/games-wp/lol.jpg\");\n}\n\n.accordion ul li:nth-child(6) {\n  background-image: url(\"/assets/images/games-wp/dota2.jpg\");\n}\n\n.accordion ul li:nth-child(7) {\n  background-image: url(\"/assets/images/games-wp/csgo.jpg\");\n}\n\n.accordion ul li:nth-child(8) {\n  background-image: url(\"/assets/images/games-wp